import './style.css'
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import * as dat from 'lil-gui'
import gsap from 'gsap'

import vertex from './shaders/vertex.glsl'
import fragment from './shaders/fragment.glsl'
import { Mesh } from 'three'

/**
 * Base
 */
// Debug
const gui = new dat.GUI()
const settings = {
    time:0,
    multiplier:0
}

// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()

// Raycaster
const raycaster = new THREE.Raycaster()
const cursor = new THREE.Vector2()
const raycasterPoint = new THREE.Vector2()


/**
 * Textures
 */
const textureLoader = new THREE.TextureLoader()
const particles = textureLoader.load('textures/1.png')
const image = textureLoader.load('textures/2.jpg')

/**
 * Test mesh
 */
// Geometry
const geometry = new THREE.BufferGeometry()
const pointsCount = 512 * 512
const positions = new THREE.BufferAttribute(new Float32Array(pointsCount * 3) , 3) 
const coords = new THREE.BufferAttribute(new Float32Array(pointsCount * 3) , 3)
const speed = new THREE.BufferAttribute(new Float32Array(pointsCount * 3) , 1)
const offset = new THREE.BufferAttribute(new Float32Array(pointsCount * 3) , 1)
const direction = new THREE.BufferAttribute( new Float32Array(pointsCount * 3) , 1)
const press = new THREE.BufferAttribute(new Float32Array(pointsCount * 3) , 1)
let index = 0

function getRandom(min, max) {
    return min + (min-max) * Math.random()
}

for(let i=0; i<512; i++){
    let posX = i - 256
    for(let j=0; j<512; j++){
        positions.setXYZ(index , posX * 2 , (j-256) * 2, 0)
        coords.setXYZ(index , posX * 2 , (j-256) * 2 ,0)
        speed.setX(index , getRandom(-1000 , 1000) )
        offset.setX(index , getRandom(0.4 , 1))
        direction.setX(index , Math.random>0.5?1:-1)
        press.setX(index , getRandom(0.4 , 1))
        index++;
    }
}
geometry.setAttribute('position' , positions)
geometry.setAttribute('aCoords' , coords)
geometry.setAttribute('aSpeed' , speed)
geometry.setAttribute('aOffset' , offset)
geometry.setAttribute('aDirection' , direction)
geometry.setAttribute('aPress' , press)


// Material
const material = new THREE.ShaderMaterial({
    side: THREE.DoubleSide,
    transparent: true,
    depthTest: false,
    depthWrite: false,
    vertexShader:vertex,
    fragmentShader:fragment,
    uniforms:{
        uTime : {value:0},
        uMultimplier:{value:0},

        uImageTexture: {value:image},
        uParticleTexture : {value:particles},

        uCursorX : {value:0},
        uCursorY : {value:0},
        uMouseClick : {value:1}
    }
})
gui.add(settings , 'multiplier').min(0).max(1).step(0.01).onChange(()=>{
    material.uniforms.uMultimplier.value = settings.multiplier
})

// Mesh
const points = new THREE.Points(geometry , material)
scene.add(points)

/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}


/**
 * Events
 */
window.addEventListener('resize', () =>{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

// window.addEventListener('scroll' , ()=>{
//     points.material.uniforms.uMultimplier.value = (window.scrollY / sizes.height) * 0.5 + 0.1
// })

window.addEventListener('mousemove' , (e)=>{
    points.material.uniforms.uCursorX.value = ( e.clientX / sizes.width -0.5 ) * 1000 
    points.material.uniforms.uCursorY.value = - ( e.clientY / sizes.height -0.5 ) * 1000
    raycaster.setFromCamera(cursor , camera)


    let intersects = raycaster.intersectObjects([])
    
})

window.addEventListener('mousedown' , ()=>{
    gsap.to(points.material.uniforms.uMouseClick , { duration:1 , value:1 , ease: 'elastic.out(1,0.3)'}) 
})
window.addEventListener('mouseup' , ()=>{
    gsap.to(points.material.uniforms.uMouseClick , { duration:1 , value:0 , ease: 'elastic.out(1,0.3)'})
})

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(70, sizes.width / sizes.height, 0.1, 3000)
camera.position.set(0, 0, 1000)
scene.add(camera)

// Controls
// const controls = new OrbitControls(camera, canvas)
// controls.enableDamping = true

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

/**
 * Animate
 */
const clock = new THREE.Clock()

const tick = () =>{
    const elapsedTime = clock.getElapsedTime()

    // Update shader 
    settings.time = elapsedTime 
    points.material.uniforms.uTime.value = settings.time
    // points.material.uniforms.uCursor.value = cursor

    // Rotate mesh
    // points.rotation.z = elapsedTime 

    // Update controls
    // controls.update()

    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()